.social-icons {
  padding: 0.25rem 0;
  font-size: 3rem; }
  @media (max-width: 425px) {
    .social-icons {
      font-size: 2.5rem; } }
  .social-icons a {
    padding: 0 0.625rem; }
    .social-icons a.devto svg {
      margin-bottom: -0.25rem;
      width: 2.7rem;
      height: 2.7rem; }
      @media (max-width: 425px) {
        .social-icons a.devto svg {
          margin-bottom: -0.225rem;
          width: 2.25rem;
          height: 2.25rem; } }
  .social-icons.animate-icons a {
    display: inline-block;
    -webkit-box-shadow: 0 3px 5px transparent;
            box-shadow: 0 3px 5px transparent;
    -webkit-transform: perspective(1000) translateZ(0);
            transform: perspective(1000) translateZ(0);
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    overflow: hidden; }
    .social-icons.animate-icons a:hover, .social-icons.animate-icons a:focus {
      -webkit-animation: icon-wobble .75s ease-in-out forwards;
              animation: icon-wobble .75s ease-in-out forwards;
      -webkit-transform: perspective(100) translateZ(0);
              transform: perspective(100) translateZ(0); }
    .social-icons.animate-icons a:visited .social-icons.animate-icons a:active {
      -webkit-transform: perspective(1000) translateZ(0);
              transform: perspective(1000) translateZ(0); }

@-webkit-keyframes icon-wobble {
  25% {
    -webkit-transform: translateY(-8px) perspective(100) translateZ(20px);
            transform: translateY(-8px) perspective(100) translateZ(20px); }
  50% {
    -webkit-transform: translateY(0px) perspective(100) translateZ(20px);
            transform: translateY(0px) perspective(100) translateZ(20px); }
  75% {
    -webkit-transform: translateY(-4px) perspective(100) translateZ(20px);
            transform: translateY(-4px) perspective(100) translateZ(20px); }
  100% {
    -webkit-transform: translateY(0px) perspective(100) translateZ(20px);
            transform: translateY(0px) perspective(100) translateZ(20px); } }

@keyframes icon-wobble {
  25% {
    -webkit-transform: translateY(-8px) perspective(100) translateZ(20px);
            transform: translateY(-8px) perspective(100) translateZ(20px); }
  50% {
    -webkit-transform: translateY(0px) perspective(100) translateZ(20px);
            transform: translateY(0px) perspective(100) translateZ(20px); }
  75% {
    -webkit-transform: translateY(-4px) perspective(100) translateZ(20px);
            transform: translateY(-4px) perspective(100) translateZ(20px); }
  100% {
    -webkit-transform: translateY(0px) perspective(100) translateZ(20px);
            transform: translateY(0px) perspective(100) translateZ(20px); } }



